import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
import ThumbnailChooser from 'components/ThumbnailChooser';
import Button from '@mui/material/Button';

import ImageOptionsDialog from 'components/ImageOptionsDialog';
import AddCaptionButton from 'components/AddCaptionButton';
import RemoveSelectedImageButton from 'components/RemoveSelectedImageButton';
import MemoryPhotoAlbum from 'components/MemoryPhotoAlbum';

export default function ImagePreviewer({
  imageDetails,
  handleRemoveImage,
  thumbnail,
  handleThumbnailChange,
  handleCaptionChange,
  //  setDimensions,
  //  resizeHeights,
}) {
  console.log(imageDetails);
  const [images, setImages] = useState([]);
  const [editingIndex, setEditingIndex] = useState(null);
  const [open, setOpen] = useState(false);

  const photos = () => imageDetails.map((photo, index) => ({
    alt: index,
    src: photo.url,
    width: photo.width,
    height: photo.height,
    caption: photo.caption || '',
  }));

  useEffect(() => setImages(photos), [imageDetails]);
  console.log(images);

  const handleClose = () => {
    setEditingIndex(null);
    setOpen(false);
  };

  const handleCaptionClick = (index) => {
    setEditingIndex(index);
    setOpen(true);
  };

  const renderPhotos = ({
    layout,
    imageProps: {
      alt, style, title, ...restImageProps
    },
  }) => (
    <Button component="div" sx={{ padding: '2px' }}>
      <img
        alt={alt}
          // eslint-disable-next-line react/jsx-props-no-spreading
        style={{ ...style, width: layout.width, padding: 0 }}
          // eslint-disable-next-line react/jsx-props-no-spreading
        {...restImageProps}
      />
      {
        layout.index !== thumbnail ? (
          <RemoveSelectedImageButton index={layout.index} handleRemoveImage={handleRemoveImage} />
        ) : null
      }
      <ThumbnailChooser
        index={layout.index}
        thumbnail={thumbnail}
        handleThumbnailChange={handleThumbnailChange}
      />
      <AddCaptionButton
        handleClick={() => handleCaptionClick(layout.index)}
        caption={('caption' in images[layout.index]) ? images[layout.index].caption : ''}
      />
    </Button>
  );

  console.log(editingIndex);
  return images.length ? (
    <Box component="div" sx={{ py: 2 }}>
      <Divider />
      <Typography sx={{ p: 2 }} variant="h6" align="center">
        Selected Images
      </Typography>
      <MemoryPhotoAlbum images={images} renderPhoto={renderPhotos} />
      {editingIndex !== null ? (
        <ImageOptionsDialog
          imageCaption={
            'caption' in images[editingIndex] ? images[editingIndex].caption : ''
          }
          index={editingIndex}
          src={imageDetails[editingIndex].url}
          open={open}
          handleClose={handleClose}
          handleSubmit={handleCaptionChange}
        />
      ) : null}
    </Box>
  ) : null;
}

ImagePreviewer.propTypes = {
  imageDetails: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string,
    url: PropTypes.string,
    size: PropTypes.number,
    width: PropTypes.number,
    height: PropTypes.number,
    caption: PropTypes.string,
  })).isRequired,
  handleRemoveImage: PropTypes.func.isRequired,
  thumbnail: PropTypes.number.isRequired,
  handleThumbnailChange: PropTypes.func.isRequired,
  handleCaptionChange: PropTypes.func.isRequired,
  //  setDimensions: PropTypes.func.isRequired,
  //  resizeHeights: PropTypes.arrayOf(PropTypes.number).isRequired,
};
